<script>
import axios from "axios";
import Loading from "../../components/loading.vue";
import StreamUploadVideo from "../admin/stream_upload_video";
import { url } from "../../main";
export default {
  components: {
    Loading,
    StreamUploadVideo,
  },
  created() {
    const params = this.$route.params;
    if (params.id) {
      this.id = params.id;
      axios.get("/fetchVideo/" + params.id).then(async (res) => {
        this.ytUrl = res.data.video.videoPath;
        this.lessonData = res.data.video;
        this.file = res.data.video.filePath;
        this.method = this.videoType;
        this.edit = true;
        this.examId = res.data.examId;
      });
    } else {
    }

    if (localStorage.getItem("chunks")) {
      this.method = "uploaded";
    }
  },
  data() {
    return {
      lessonData: {
        name: "",
        number: 1,
        part: "",
        partNumber: 1,
        stage: "6",
        explain: "",
        section: 1,
        paid: false,
        videoPath: null,
        file: null,
        showDate: new Date().toISOString().substring(0, 10),
        expiration: new Date(new Date().getTime() + 60 * 24 * 3600 * 1000)
          .toISOString()
          .substring(0, 10),
      },
      id: null,
      edit: false,
      progress: 0,
      method: "embedded",
      loading: false,
      url: axios.defaults.baseURL,
      file: "",
      ytUrl: "",
      examId: null,
    };
  },
  computed: {
    formData() {
      const formData = new FormData();
      formData.append("name", this.lessonData.name);
      formData.append("number", this.lessonData.number);
      formData.append("part", this.lessonData.part);
      formData.append("partNumber", this.lessonData.partNumber);
      formData.append("stage", this.lessonData.stage);
      formData.append("videoPath", this.lessonData.videoPath);
      formData.append("date", new Date());
      formData.append("showDate", this.lessonData.showDate);
      formData.append("expiration", this.lessonData.expiration);
      formData.append("file", this.lessonData.file);
      formData.append("explain", this.lessonData.explain);
      formData.append("section", this.lessonData.section);
      formData.append("paid", this.lessonData.paid);
      formData.append("fileName", this.filesName);
      return formData;
    },
    filesName() {
      return `${this.lessonData.section}_${this.lessonData.name}_${this.lessonData.number}_${this.lessonData.stage}`;
    },
    fileName() {
      return this.file || "";
    },
    videoType() {
      console.log(this.lessonData);
      const vid = this.lessonData.videoPath;
      if (!vid?.includes("http") && vid?.includes(".mp4")) return "uploaded";
      else return "embedded";
    },
    adminData() {
      return this.$store.getters.adminData;
    },
    sections() {
      return this.$store.getters.sections.filter(
        (sec) => sec.stage == this.lessonData.stage
      );
    },
    lessons() {
      return this.$store.getters.lessons.filter((lesson) => {
        return (
          lesson.stage == this.lessonData.stage &&
          lesson.section == this.lessonData.section
        );
      });
    },
  },
  watch: {
    ytUrl(val) {
      const links = val.split(",");
      ////////////////////////////////////////////////////
      if (!val || val == "" || val == "null" || val == undefined || val == "undifined")
        return (this.lessonData.videoPath = null);
      if (val.includes("vimeo")) return (this.lessonData.videoPath = val);
      const slashes = val.split("/");
      const length = slashes.length;
      let id = "";
      if (val.includes("youtube") && val.includes("/watch")) {
        id = val.split("v=")[1];
      } else if (
        val.includes("youtube") &&
        (val.includes("/shorts") || val.includes("/video")) &&
        val.includes("/edit")
      ) {
        id = slashes[length - 2];
      } else if (
        val.includes("youtube") &&
        (val.includes("/shorts") || val.includes("/video") || val.includes("/embed"))
      ) {
        id = slashes[length - 1];
      } else if (val.includes("youtu.be")) {
        id = slashes[length - 1];
      } else {
        this.lessonData.videoPath = "invalid";
      }
      id = id.split("?").length > 0 ? id.split("?")[0] : id;
      this.lessonData.videoPath = `https://www.youtube.com/embed/${id}`;
    },
  },
  methods: {
    getFile(event) {
      const file = event.target.files[0];

      if (file == undefined) {
        this.resetFile();
        return;
      }
      if (
        ![
          "jpg",
          "jpeg",
          "png",
          "pdf",
          "mp4",
          "mpeg",
          "mp3",
          "txt",
          "avi",
          "3gp",
          "rar",
          "zip",
          "plain",
        ].includes(file.type.split("/")[1])
      ) {
        if (file.type.length > 0) return alert("    ");
      }
      if (file.size > 10000000) {
        alert("     ");
        return;
      }
      this.file = file.name;
      this.lessonData.file = file;
    },
    ////////////////////////////////////////
    handleVideoFileUpload(videoFile) {
      this.lessonData.videoPath = null;
      this.formData.set("videoPath", videoFile);
      this.uploadVideo();
    },
    ////////////////////////////////////////
    uploadVideo() {
      if (!this.lessonData.section) return alert("   ");
      if (this.lessonData.videoPath == "invalid") return alert("   ");
      // if (!this.lessonData.name || !this.lessonData.section || !this.lessonData.stage) {
      // }
      this.loading = true;
      let path = this.edit ? "/editVideo/" + this.id : "/uploadVideo";
      axios
        .post(path, this.formData, {
          onUploadProgress: (progress) => {
            var val = Math.floor((progress.loaded / progress.total) * 100);
            this.progress = val === 100 ? 0 : val;
          },
        })
        .then((res) => {
          this.loading = false;
          if (res.data.done) {
            console.log(res.data.videoName);
            this.lessonData.videoPath = res.data.videoName;
            this.$store.dispatch("writemessage", "   ");
            this.id = res.data.id;
          } else {
            this.$store.dispatch("writemessage", res.data.err);
          }
        });
    },
    /////////////////////////////////////////////////
    downloadFile(id) {
      const fileUrl = this.url + "/downloadFile/" + id;
      window.open(fileUrl, "_blank");
    },
    resetFile() {
      this.file = "";
      this.lessonData.file = "";
      this.$refs.file.value = "";
    },
    removeTask(id) {
      if (!confirm("  !")) return;
      this.loading = true;
      axios.delete("/removeTask/" + id).then((res) => {
        if (res.data.err) this.$store.dispatch("writemessage", res.data.err);
        this.loading = false;
      });
    }, /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    async copyLesson() {
      await localStorage.setItem("lesson", JSON.stringify(this.lessonData));
      this.$store.dispatch("writemessage", "  ");
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    pasteLesson() {
      const lesson = JSON.parse(localStorage.getItem("lesson"));
      if (lesson) {
        this.ytUrl = lesson.videoPath;
        this.lessonData = lesson;
        this.file = lesson.filePath;
        // this.examId = res.data.examId;
      }
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
  },
};
</script>

